$backgroundColor: #8d612e;
$radius: 40%;

form {
  display: inline-block;
  margin: auto;
  width: 200px;

  label {
    display: flex;
    margin: 1rem 0;

    &.methods {
      span {
        cursor: pointer;
        margin-right: 1rem;
        padding: .25rem .5rem;
        background: rgb(37, 90, 92);

        &:hover, &.active {
          background: rgb(255, 255, 255);
        }
      }
    }
  }
}
